<template>
  <div id="me">
    <head-top title="我的设备"></head-top>
    <ul class="deviceList">
      <li
        @touchstart="chooseItem($event, 'start', item)"
        @touchmove="chooseItem($event, 'move', item)"
        @touchend="chooseItem($event, 'end', item)"
        class="list_li"
        v-for=" item in deviceList"
        :key="item.id"
      >
        <div
          class="left"
          ref="sideLeft"
        >
          <img
            src="../../assets/device/socket@3x.png"
            alt=""
            v-if="false"
          >
          <img
            src="../../assets/device/socket_off@3x.png"
            alt=""
            v-if="true"
          >
          <div class="deviceName">
            <span>{{item.name}}</span>
            <span v-if="item.state">已开启</span>
            <span
              class="deviceOFF"
              v-if="!item.state"
            >已关闭</span>
          </div>
          <span class="currTemp">当前温度:<font style="color:#3551c1"> {{item.currTemp}}℃</font></span>
        </div>
        <div class="side">
          <div
            class="delate"
            @click="remove(item)"
          >
            <span>Delete</span>
          </div>
        </div>
      </li>
    </ul>
    <div
      class="adDevice"
      @click="add"
    >
      <img
        src="../../assets/device/add@3x.png"
        alt=""
      >
      <span>添加设备</span>
    </div>
    <!-- 删除设备 -->
    <messageBox
      setHeight="150"
      setType="confirm"
      v-show="messageBoxType === 'Deletedevice'"
    >
      <div
        slot="confirmColumn"
        class="common_alert"
      >
        <div class="alert_icon">
          <span>删除设备</span>
        </div>
        <div class="alert_describe">删除后需要重新扫描绑定，删除吗？</div>
        <div class="alert_operation">
          <div
            class="determine_one"
            @click="alertChoose(0)"
          >取消</div>
          <div
            class="determine_one ok"
            @click="alertChoose(1)"
          >确定</div>
        </div>
      </div>
    </messageBox>
    <transition
      mode="in-out"
      name="slide-left"
    >
      <router-view></router-view>
    </transition>
  </div>
</template>

<script>
import messageBox from '../../components/common/messageBox'
import { mapActions, mapGetters } from 'vuex'
import headTop from '../../components/common/head'
import {
  temperatureAndHumidityDataRequest
  // callThePhoneScanRequest
} from '../../api/method'
import {
  temperatureAndHumidityDataResponse,
  deviceListResponse,
  callThePhoneScanResponse
} from '../../api/getData'
import { getStore } from '../../utils/index'
export default {
  data () {
    return {
      flagdelter: true, // 左滑
      startX: null, // 水平移动距离
      startY: null, // 垂直移动距离
      delterflag: false, // true 左滑按钮显示
      endX: null,
      active: 'me',
      currTemperature: 16,
      // deviceList: {
      //   name: '供暖插座',
      //   state: true,
      //   currTemp: 23,
      //   mac: ''
      // },
      deviceList: [{
        name: '供暖插座',
        state: true,
        currTemp: 23,
        mac: ''
      }],
      disJump: true
    }
  },
  components: {
    'head-top': headTop,
    messageBox
  },
  computed: {
    ...mapGetters(['messageType', 'currentRouter', 'devicesData']),
    messageBoxType () {
      return this.messageType
    },
    devicesListformat () {
      let obj = this.devicesData
      console.log(this.devicesData)
      return obj
    }
  },
  created () {
    temperatureAndHumidityDataRequest(this.NAVIGATOR, this)
  },
  mounted () {
    temperatureAndHumidityDataResponse(this.NAVIGATOR, this, 'device')
    callThePhoneScanResponse(this.NAVIGATOR, this)
    deviceListResponse(this.NAVIGATOR, this)
    if (this.$root.ISDEBUG) {
      // window.deviceListResponse('[{"name":"Hearter","switch":false,"icon":"heartIMG","status":"25%","mac":"00:00:01:00:00","encrypted":true,"isBinding":true,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}},{"name":"111","switch":false,"icon":"heartIMG","status":"50%","mac":"00:00:02:00:00","encrypted":true,"isBinding":true,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}}]')
      // window.deviceListResponse('[{"name":"Humidity","switch":false,"icon":"heartIMG","status":"50%","mac":"00:00:02:00:00","encrypted":true,"isBinding":true,"wifiInfomation":{"ssid":"PHICOMM_startai","strength":4}}]')
      window.temperatureAndHumidityDataResponse('00:00:02:00:00', '{"temperature":{"currentValue":0,"hotAlarmValue":20,"hotAlarmSwitch":false,"codeAlarmValue":20,"codeAlarmSwitch":false},"humidity":{"currentValue":1,"hotAlarmValue":50,"codeAlarmValue":10,"hotAlarmSwitch":true,"codeAlarmSwitch":true}}')
    }
    let obj = JSON.parse(getStore('HisTempData'))
    if (obj) {
      this.deviceList[0].currTemp = obj[0].currtemp
    }
  },
  methods: {
    ...mapActions(['showMessagesBox', 'setDevicesdata', 'setCurrentMac', 'loginStatus']),
    add () {
      if (this.loginStatus) {
        console.log('callThePhoneScanRequest')
        // callThePhoneScanRequest(this.NAVIGATOR, this)
      } else {
        this.$router.push({ path: '/login' })
      }
    },
    // 左滑
    chooseItem (e, type, item) {
      let Listli = e.target
      while (Listli.className !== 'list_li') {
        Listli = Listli.parentNode
        if (Listli.className === 'side') {
          return false
        }
      }
      let Slistli = document.getElementsByClassName('list_li')
      this.liNode = Slistli
      switch (type) {
        case 'start':
          this.hideDelet()
          // this.isMove = false
          this.startX = e.changedTouches['0'].pageX
          this.startY = e.changedTouches['0'].pageY
          break
        case 'move':
          //   this.isMove = true
          let liLeft = e.changedTouches['0'].pageX - this.startX
          let moveY = e.changedTouches['0'].pageY - this.startY
          console.log(Math.abs(moveY))
          if (liLeft < -35 && this.flagdelter && Math.abs(moveY) < 40) {
            if (liLeft < -120) {
              this.delterflag = true
              Listli.style.marginLeft = -120 + 'px'
              console.log(this.$refs.sideLeft[0])
              this.$refs.sideLeft[0].style.marginRight = 0 + 'px'
            } else if (liLeft > 0) {
              Listli.style.marginLeft = 0 + 'px'
              this.$refs.sideLeft[0].style.marginRight = 25 + 'px'
            } else {
              if (liLeft === -120) {
                console.log(2)
                this.delterflag = true
              }
              Listli.style.marginLeft = liLeft + 'px'
            }
          } else {
            this.hideDelet()
          }
          // }
          // console.log(e.changedTouches["0"].pageX)
          break
        case 'end':
          liLeft = e.changedTouches['0'].pageX - this.startX
          moveY = e.changedTouches['0'].pageY - this.startY
          if (Math.abs(moveY) < 25) {
            this.endX = e.changedTouches['0'].pageX
            if ((this.startX - this.endX) < 120 && (this.startX - this.endX) > 0) {
              let x = this.endX - this.startX
              let timer = setInterval(() => {
                if (x > -120) {
                  x += -10
                } else {
                  x = -120
                  // this.$refs.sideLeft[0].style.marginRight = 0 + 'px'
                  console.log(this.$refs.sideLeft)
                  this.$refs.sideLeft.forEach(Element => {
                    console.log(Element)
                    Element.style.marginRight = 0 + 'px'
                  })
                  this.delterflag = true // 删除显示
                  clearInterval(timer)
                }
                Listli.style.marginLeft = x + 'px'
              }, 30)
            }
          }
          console.log(Math.abs(this.endX - this.startX))
          if (Math.abs(this.endX - this.startX) === 0 && !this.delterflag) {
            if (!this.disJump) return false
            this.$router.push({ path: 'device/temperature' })
            // this.setCurrentMac(item.mac)
            this.disJump = false
          }
          // 判断左滑还是点击
          // Slistli = document.getElementsByClassName('list_li')
          break
      }
    },
    hideDelet () {
      let Slistli = document.getElementsByClassName('list_li')
      for (let i = 0; i < Slistli.length; i++) {
        if (parseInt(Slistli[i].style.marginLeft) < 0) {
          this.flagdelter = false
          let x = parseInt(Slistli[i].style.marginLeft)
          let timer = setInterval(() => {
            if (x < 0) {
              x += 10
            } else {
              x = 0
              this.flagdelter = true
              this.delterflag = false
              clearInterval(timer)
              this.$refs.sideLeft[0].style.marginRight = '18px'
              this.$refs.sideLeft.forEach(Element => {
                console.log(Element)
                Element.style.marginRight = 18 + 'px'
              })
            }
            if (Slistli[i]) {
              Slistli[i].style.marginLeft = x + 'px'
            }
          }, 30)
        }
      }
    },
    remove () {
      // if (this.active === 1) {
      //   this.removeType = 'Remove Devices' // 提示语
      //   this.messageToastType = 'Removing the device'
      //   this.setCurrentMac(item.mac) // 删除设备标识
      // } else {
      //   this.messageToastType = 'Removing the room'
      //   this.removeType = 'Remove Rooms'
      //   this.currentRoomFlag = item.id // 删除room标识
      // }
      this.showMessagesBox({ 'type': 'Deletedevice' })
    },
    alertChoose (num) {
      switch (num) {
        case 0:
          this.showMessagesBox({ 'type': 'null' })
          break
        case 1:
          this.showMessagesBox({ 'type': 'null' })
          this.hideDelet()
          break
        default:
          break
      }
    }
  },
  watch: {
    currentRouter (newnval) {
      console.log(newnval)
      if (newnval === 'device') {
        this.disJump = true
        temperatureAndHumidityDataRequest(this.NAVIGATOR, this)
        temperatureAndHumidityDataResponse(this.NAVIGATOR, this, 'device')
        let obj = JSON.parse(getStore('HisTempData'))
        if (obj) {
          this.deviceList[0].currTemp = obj[0].currtemp
        }
      }
    },
    deviceList (newnval) {
      if (newnval[0].currTemp === 100) {
        newnval[0].currTemp = 'N/A'
      }
    }
  }
}
</script>

<style scoped lang="less">
#me {
  .wh(100%, 100%);
  display: flex;
  flex-direction: column;
  font-size: 30px;
  color: #1b1b1b;
  .deviceList {
    margin: 0px 0px 0 25px;
    li {
      .wh(132%, 226px);
      display: flex;
      justify-content: space-between;
      margin-top: 25px;
      .side {
        .wh-l(240px, 100%);
        display: flex;
        flex-direction: row;
        font-size: 20px;
        color: #fcfcfc;
        // margin-left: 30px;
        .delate {
          width: 100%;
          background: #e93e3e;
          padding-top: 27px;
          font-size: 36px;
          line-height: 180px;
          box-shadow: 2px 2px 5px 2px #e93e3e;
        }
      }
    }
    li .left {
      display: flex;
      .wh(78%, 226px);
      background: #eeeeee;
      box-shadow: 2px 2px 5px 2px #d4d1d1;
      margin-right: 30px;
      img {
        .wh(94px, 94px);
        align-self: center;
        margin-left: 54px;
      }
      .deviceName {
        height: 94px;
        align-self: center;
        display: flex;
        flex-direction: column;
        margin-left: 47px;
        span:last-child {
          font-size: 27px;
          color: #17c569;
          margin-top: 28px;
        }
        .deviceOFF {
          color: #e94646;
        }
      }
      .currTemp {
        align-self: center;
        margin-left: 47px;
      }
    }
  }
  .adDevice {
    display: flex;
    .wh(100%, auto);
    justify-content: center;
    img {
      .wh(42px, 42px);
      margin-right: 10px;
    }
    position: absolute;
    bottom: 145px;
  }
  .common_alert {
    /deep/.determine_one {
      .wh-l(50%, 76px);
      margin: 0 52px 28px 78px;
      background: #aaaaaa;
      border-radius: 10px;
    }
    .ok {
      background: #e94646;
    }
    .alert_icon {
      margin-top: 41px;
    }
    .alert_describe {
      margin-top: 45px;
      color: #303030;
    }
  }
}
</style>



